import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      recoms: [],
      posters:[],
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(e) {
    var data = require('./movie.json');
    var recos = data[this.state.value][0]['recos'];
    console.log(this.state.value);
    console.log(data[this.state.value][0]);
    console.log(recos);
    this.setState({recoms: recos});  
    console.log(this.state.recoms);
    e.preventDefault();  
  }

  renderList(){
    console.log("RenderList")
    var recos = this.state.recoms;
    console.log(recos);

    recos.map((reco) => {
        var url = 'http://www.omdbapi.com/?t=' + reco;
        fetch(url)
        .then(response =>response.json())
        .then(data => {
          console.log(data);
        })
    });
  }

  render() {
    return (
      <div>      
        <form onSubmit={this.handleSubmit}>
        <label>
          Name:
          <input type="text" value={this.state.value} onChange={this.handleChange} />
        </label>
        <input type="submit" value="Submit" onClick = {this.handleSubmit.bind(this)}/>
      </form>
     {this.renderList()}
</div>
    );
  }
}

export default App;
